.product-detail {
    margin: 76px 0 80px;
    & > .title {
        font-size: 20px;
        color: #1f1f1f;
    }
    & > .info {
        margin-top: 12px;
        font-size: 12px;
        color: #898989;
        .category {
            color: #1f1f1f;
        }
        span {
            position: relative;
            padding: 0 9px 0 10px;
            &:first-child {
                padding-left: 0;
            }
            &:first-child:before {
                display: none;
            }
            &:before {
                content: '';
                position: absolute;
                top: 2px;
                left: 0;
                width: 1px;
                height: 10px;
                background-color: #ddd;
            }
            .icon {
                margin-left: 6px;
                padding: 2px 5px;
                font-size: 11px;
                color: #fff;
                background: #aaa;
            }
        }
    }
    .page-function {
        margin-top: -30px;
        margin-right: 11px;
    }
    .favorite-pop {
        display: none;
        position: fixed;
        top: 50%;
        left: 50%;
        width: 232px;
        height: 232px;
        padding: 82px 0;
        background: url('/_ui/img/pc/bg-favorite.png') no-repeat center;
        text-align: center;
        box-sizing: border-box;
        transform: translate(-50%,-50%);
        z-index: 100;
        i {
            display: block;
            width: 26px;
            height: 22px;
            margin: 0 auto 12px;
            background: url('/_ui/img/pc/icn/icn-favorite.png') no-repeat;
            background-size: contain;
        }
        .ico-on {
            background: url('/_ui/img/pc/icn/icn-favorite-on.png') no-repeat;
            background-size: contain;
        }
        p {
            display: none;
            font-family: "Noto Sans KR";
            font-size: 14px;
            line-height: 20px;
        }
        .txt-on {
            display: block;
        }
    }
    .detail-prod {
        margin-top: 45px;
        &::after {
            content: '';
            display: table;
            clear: both;
        }
        .thumb-group {
            position: relative;
            float: left;
            width: 830px;
            .thumb {
                overflow: hidden;
                .swiper-slide {
                    width: 830px;
                    img {
                        //position: relative;
                        //display: block;
                        //width: 49%;
                        //&:first-child {
                        //    margin-right: 4px;
                        //}
                        //&:last-child {
                        //    margin-left: 4px;
                        //}
                    }
                    .pannel {
                        position: relative;
                        display: block;
                        width: 49%;
                        cursor: zoom-in;
                        .mask {
                            overflow: hidden;
                        }
                        img {
                            width: 100%;
                            transition: .1s;
                        }
                        &:first-child {
                            margin-right: 5px;
                        }
                        &:last-child {
                            margin-left: 5px;
                        }
                        &::before {
                            content: '';
                            position: absolute;
                            display: block;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            background: #000;
                            opacity: .03;
                            z-index: 1;
                        }
                    }
                }
            }
            .swiper-thumb {
                overflow: hidden;
                margin-top: 30px;
                a {
                    position: relative;
                    display: inline-block;
                    width: 118px;
                    height: 90px;
                    margin: 0 8px 8px;
                    &.on {
                        &::before {
                            content: '';
                            position: absolute;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            height: 4px;
                            background-color: #1f1f1f;
                        }
                    }
                }
                img {
                    width: 100%;
                    // width: 48%;
                    // float: left;
                }
                .pannel {
                    position: relative;
                    display: block;
                    width: 48%;
                    float: left;
                    &:first-child {
                        margin-right: 1px;
                    }
                    &:last-child {
                        margin-left: 1px;
                    }
                    &::before {
                        content: '';
                        position: absolute;
                        display: block;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        left: 0;
                        background: #000;
                        opacity: .03;
                        z-index: 1;
                    }
                }
            }
            .swiper-controls {
                button {
                    top: 50%;
                }
            }
        }
        .info-group {
            float: right;
            width: 302px;
            margin-right: 33px;
            .price {
                margin-top: 42px;
                font-size: 24px;
                color: #1f1f1f;
                del {
                    margin-left: 10px;
                    font-size: 17px;
                    color: #9ba4b0;
                }
                .sale {
                    float: right;
                    margin-top: 6px;
                    font-size: 18px;
                    margin-right: 4px;
                    padding-left: 16px;
                    color: #1f1f1f;
                    background: url('/_ui/img/pc/icn/icn-sale-lg.png') no-repeat 0 50%;
                }
            }
            .detail {
                margin-top: 38px;
                & > dl {
                    &::after {
                        content: '';
                        display: table;
                        clear: both;
                    }
                    &.sort {
                        border-bottom: 1px solid #eee;
                        dt {
                            padding-top: 6px;
                        }
                    }
                    dt, dd {
                        font-size: 12px;
                        color: #1f1f1f;
                    }
                    dt {
                        float: left;
                        font-weight: bold;
                    }
                    dd {
                        margin: 25px 0 25px 92px;
                        &::after {
                            content: '';
                            display: table;
                            clear: both;
                        }
                        input[type="radio"] {
                            display: none;
                        }
                        label {
                            display: inline-block;
                            font-size: 11px;
                            color: #666;
                            cursor: pointer;
                        }
                        .color {
                            font-size: 0;
                            &::after {
                                content: '';
                                display: table;
                                clear: both;
                            }
                            li {
                                position: relative;
                                display: inline-block;
                                margin-right: 5px;
                                margin-bottom: 5px;
                                img {
                                    position: relative;
                                    width: 25px;
                                    height: 25px;
                                    vertical-align: middle;
                                    font: 0/0 a;
                                    border: 1px solid rgba(0, 0, 0, 0.1);
                                }
                                .checked {
                                    &::before{
                                        content: '';
                                        position: absolute;
                                        left: 0;
                                        right: 0;
                                        top: 0;
                                        bottom: 0;
                                        background: url(/_ui/img/pc/icn/icn-check11.png) no-repeat center;
                                        z-index: 1;
                                    }
                                }
                                // input[type="radio"]:checked + label::before {
                                //     content: '';
                                //     position: absolute;
                                //     left: 0;
                                //     right: 0;
                                //     top: 0;
                                //     bottom: 0;
                                //     background: url(/_ui/img/pc/icn/icn-checked.png) no-repeat;
                                //     background-size: cover;
                                // }
                            }
                        }
                        .size-box-wrap {
                            position: relative;
                            .size-box {
                                li {
                                    float: left;
                                    min-width: 27px;
                                    height: 27px;
                                    margin-right: 5px;
                                    margin-bottom: 5px;
                                    &.size-sold-out {
                                        label {
                                            color: #ddd;
                                            border-color: #e5e5e5;
                                            background: url('/_ui/img/pc/icn/icn-size-none.png') no-repeat 50% 50%;
                                            background-size: cover;
                                        }
                                    }
                                    label {
                                        height: 25px;
                                        min-width: 19px;
                                        padding: 0 3px;
                                        line-height: 25px;
                                        text-align: center;
                                        color: #1f1f1f;
                                        border: 1px solid #000;
                                    }
                                    input[type="radio"] {
                                        &:checked + label {
                                            color: #fff;
                                            border: 1px solid #1f1f1f;
                                            background-color: #1f1f1f;
                                        }
                                    }
                                }
                            }
                            .size-box-slide {
                                overflow: hidden;
                            }
                            .swiper-controls {
                                .swiper-button-prev,
                                .swiper-button-next {
                                    width: 25px;
                                    height: 25px;
                                    margin-top: -13px;
                                    background-repeat: no-repeat;
                                    background-position: 50% 50%;
                                    &.swiper-button-disabled {
                                        display: none;
                                    }
                                }
                                .swiper-button-prev {
                                    left: -30px;
                                    background-image: url('/_ui/img/pc/icn/icn-swipe-prev-sm.png');
                                }
                                .swiper-button-next {
                                    right: -2px;
                                    background-image: url('/_ui/img/pc/icn/icn-swipe-next-sm.png');
                                }
                            }
                        }
                        .underline {
                            float: right;
                            display: inline-block;
                            margin-top: -4px;
                            font-size: 12px;
                            padding-bottom: 1px;
                            border-bottom: 1px solid #1f1f1f;
                        }
                    }
                    .dd-color {
                        margin-bottom: 16px;
                    }
                    .dd-size {
                        margin-top: 16px;
                        margin-bottom: 19px
                    }
                    .amount {
                        margin-top: 19px;
                        margin-bottom: 23px;
                        .count {
                            display: inline-block;
                            width: 160px;
                            background: transparent;
                        }
                        button {
                            display: inline-block;
                            width: 14px;
                            height: 25px;
                            font: 0/0 a;
                            background-repeat: no-repeat;
                            background-position: 50% 50%;
                            &.plus {
                                background-image: url('/_ui/img/pc/icn/icn-plus.png');
                            }
                            &.minus {
                                background-image: url('/_ui/img/pc/icn/icn-minus.png');
                                margin-left: 15px;
                            }
                        }
                    }
                    .delivery {
                        .delivery-type {
                            &::after {
                                content: '';
                                display: table;
                                clear: both;
                            }
                            li {
                                display: inline-block;
                                margin-left: 7px;
                                &:first-child {
                                    margin-left: 0;
                                }
                                .radio {
                                    position: relative;
                                    display: block;
                                    padding-right: 13px;
                                    label {
                                        display: inline-block;
                                        font-size: 12px;
                                        color: #cdcdcd;
                                        white-space: nowrap;
                                    }
                                    i {
                                        position: absolute;
                                        top: 2px;
                                        right: 0;
                                        width: 10px;
                                        height: 8px;
                                        vertical-align: middle;
                                        background: #fff url('/_ui/img/pc/icn/icn-radio-checked.png') no-repeat;
                                    }
                                    input[type="radio"]:checked + label {
                                        color: #1f1f1f;
                                    }
                                    input[type="radio"] + label + i {
                                        opacity: 0.3;
                                    }
                                    input[type="radio"]:checked + label + i {
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                        .today {
                            margin-top: 15px;
                            margin-left: -92px;
                            font-size: 12px;
                            color: #898989;
                            line-height: 1.4;
                        }
                        .pickup {
                            display: none;
                            margin-left: -92px;
                            dt {
                                font-weight: normal;
                            }
                            dd {
                                margin: 15px 0 15px 92px;
                                color: #898989;
                                li {
                                    margin-top: 8px;
                                    &:first-child {
                                        margin-top: 0;
                                    }
                                }
                                button {
                                    margin-top: 15px;
                                    font-size: 12px;
                                    position: relative;
                                    display: inline-block;
                                    padding-right: 10px;
                                    color: #1f1f1f;
                                    background: url('/_ui/img/pc/icn/icn-arrow-dotted.png') no-repeat 100% center;
                                }
                            }
                            p {
                                margin-top: 15px;
                                padding-top: 15px;
                                font-size: 12px;
                                color: #aaa;
                                line-height: 1.4;
                                border-top: 1px solid #f5f5f5;
                            }
                        }
                        .pickup.on {
                            display: block;
                        }
                    }
                }
                dl.payment {
                    dd {
                        margin: 22px 0 22px 92px;
                    }
                    dd.point {
                        margin-top: 32px;
                    }
                    dd.delivery {
                        margin-bottom: 26px;
                    }
                    dd.delivery-cost {
                        margin-top: 26px;
                    }
                }
                .set-product {
                    .set-prdt-image {
                        position: relative;
                        display: inline-block;
                        width: 58px;
                        height: 88px;
                        margin-top: 28px;
                        &::before {
                            content: '';
                            position: absolute;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            background: #000;
                            opacity: .03;
                            z-index: 1;
                        }
                        img {
                            width: 100%;
                        }
                        &:first-child {
                            margin-top: 5px;
                        }
                    }
                    .set-prdt-info {
                        position: relative;
                        display: inline-block;
                        width: 208px;
                        margin-top: 28px;
                        margin-left: 28px;
                        vertical-align: top;
                        p {
                        word-break: keep-all;
                        }
                        .set-title {
                            width: 225px;
                            margin-top: -4px;
                            font-size: 14px;
                            color: #1f1f1f;
                            line-height: 20px;
                        }
                        .box {
                            margin-top: 3px;
                            .set-price {
                                float: right;
                                font-size: 13px;
                                color: #1f1f1f;
                            }
                            .set-code {
                                display: inline-block;
                                font-family: 'helvetica';
                                font-size: 11px;
                                color: #aaa;
                                vertical-align: middle;
                            }
                        }
                        &:nth-child(2) {
                            margin-top: 5px;
                        }
                    }
                    dt {
                        padding-top: 30px;
                    }
                    dd {
                        margin: 0;
                        padding: 22px 0 22px 92px;
                        border-bottom: 1px solid #eee;
                    }
                }
                .coupon {
                    position: relative;
                    margin-bottom: 10px;
                    padding: 18px 0 18px 20px;
                    background-color: #fafafa;
                    .title {
                        display: block;
                        max-width: 220px;
                        margin-bottom: 12px;
                        font-size: 14px;
                        color: #1f1f1f;
                        font-weight: normal;
                        line-height: 16px;
                    }
                    .sale {
                        padding-right: 15px;
                        font-size: 15px;
                        em {
                            font-weight: bold;
                        }
                    }
                    .date {
                        font-size: 12px;
                        color: #aaa;
                    }
                    .down {
                        position: absolute;
                        display: inline-block;
                        top: 50%;
                        right: 18px;
                        width: 37px;
                        height: 39px;
                        font: 0/0 a;
                        background-color: #f23d4e;
                        background-image: url('/_ui/img/pc/icn/icn-coupon-down.png');
                        background-position: 50% 50%;
                        background-repeat: no-repeat;
                        transform: translateY(-50%);
                    }
                }
                .sold-out {
                    margin-top: 8px;
                    padding-bottom: 33px;
                    text-align: center;
                    font-size: 13px;
                    color: #898989;
                    border-bottom: 1px solid #eee;
                    strong {
                        display: block;
                        margin-bottom: 4px;
                        font-size: 15px;
                        color: #f23d4e;
                    }
                }
                .hot-item,
                .reser-item {
                    margin-top: 30px;
                    text-align: center;
                    font-size: 12px;
                    color: #898989;
                    strong {
                        color: #f23d4e;
                    }
                }
                .btn-wrap {
                    margin-top: 20px;
                    text-align: center;
                    &.col-1 {
                        .btn {
                            width: 100%;
                        }
                    }
                    &.col-2 {
                        .btn {
                            width: 145px;
                        }
                    }
                }
                .special {
                    margin-top: 30px;
                    dt {
                        float: none;
                        display: block;
                    }
                    dd {
                        margin: 15px 0 0;
                        padding-bottom: 12px;
                        border-bottom: 3px solid #1f1f1f;
                        li {
                            background: url('/_ui/img/pc/icn/icn-arrow-dotted.png') no-repeat 100% center;
                        }
                        a {
                            display: block;
                            padding: 5px 0;
                            width: 270px;
                            font-size: 13px;
                            color: #1f1f1f;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                        }
                    }
                }
                .banner {
                    margin-top: 30px;
                    a {
                        display: block;
                        margin-top: 8px;
                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }
        }
    }
    .detail-nav {
        margin: 100px 0 45px;
        font-weight: bold;
        border-top: 3px solid #1f1f1f;
        border-bottom: 1px solid #f0f0f0;
        background-color: #fff;
        &.fixed {
            height: 68.33px;
            ul {
                position: fixed;
                top: 0;
                left: 50%;
                z-index: 2;
                width: 1200px;
                margin-left: -600px;
                margin-top: 0;
                background: #fff;
                border-top: 3px solid #1f1f1f;
                border-bottom: 1px solid #f0f0f0;
            }
            
        }
        ul {
            text-align: center;
            li {
                display: inline-block;
                &.on {
                    a {
                        color: #1f1f1f;
                    }
                }
                a {
                    display: inline-block;
                    padding: 24px 73px;
                    font-size: 17px;
                    color: #aaa;
                    font-weight: 500;
                }
            }
        }
    }
    .prod-cont {
        width: 980px;
        margin: 0 auto;
        .title {
            margin-top: 100px;
            margin-bottom: 45px;
            font-size: 20px;
            color: #1f1f1f;
            font-weight: bold;
        }
        .product-info {
            line-height: 1.8;
            font-size: 14px;
            color: #1f1f1f;
            .area-info-img {
                width: 100%;
                height: auto;
            }
            .area-info-txt {
                margin-top: 30px;
                li {
                    margin-top: 14px;
                    font-size: 14px;
                    color: #1f1f1f;
                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
            .area-add-info {
                overflow: hidden;
                position: relative;
                margin-top: 60px;
                img {
                    float: left;
                    width: 80px;
                    height: 80px;
                }
                & > div {
                    margin-top: 14px;
                    padding-left: 99px;
                    strong {
                        display: block;
                        margin-bottom: 12px;
                        font-size: 14px;
                        font-weight: bold;
                        color: #1f1f1f;
                    }
                    p {
                        font-size: 12px;
                        color: #898989;
                        line-height: 16px;
                    }
                }
            }
            .detail-images {
                margin-top: 84px;
                .box-img {
                    max-width: 720px;
                    margin: 15px auto 0;
                    img {
                        width: auto;
                        max-width: 720px;
                    }
                }
            }
        }
        .survey {
            .size-guide-img {
                float: left;
                width: 300px;
                img {
                    width: 100%;
                    margin: 0 auto;
                }
            }
            dl {
                padding: 30px 0 20px 330px;
                font-size: 14px;
                color: #1f1f1f;
                // background: url('/_ui/img/pc/bg-product-survey.jpg') no-repeat 78px top;
                &::after {
                    content: '';
                    display: table;
                    clear: both;
                }
                dt {
                    float: left;
                    height: 38px;
                    span {
                        display: inline-block;
                        width: 18px;
                        height: 18px;
                        margin-top: -2px;
                        margin-right: 10px;
                        text-align: center;
                        vertical-align: middle;
                        line-height: 18px;
                        color: #fff;
                        border-radius: 50%;
                        background-color: #1f1f1f;
                    }
                }
                dd {
                    height: 38px;
                    margin-left: 125px;
                }
            }
            .img-size {
                width: 455px;
            }
            .wrap-table {
                overflow-x: scroll;
                table {
                    // width: 100%;
                    margin-top: 45px;
                    font-size: 12px;
                    color: #1f1f1f;
                    border-collapse: collapse;
                    caption {
                        display: none;
                    }
                    thead {
                        border-top: 2px solid #1f1f1f;
                    }
                    tbody {
                        border-bottom: 2px solid #1f1f1f;
                    }
                    th, td {
                        min-width: 115px;
                        max-width: 115px;
                        padding: 14px 0;
                    }
                    th {
                        text-align: center;
                        font-weight: bold;
                        border-bottom: 2px solid #1f1f1f;
                    }
                    td {
                        text-align: center;
                        border-bottom: 1px solid #f1f1f1;
                        &:first-child {
                            text-align: left;
                        }
                    }
                    .title {
                        min-width: 175px;
                        max-width: 175px;
                    }
                }
            }
            .bul-list {
                margin-top: 15px;
                li {
                    position: relative;
                    margin-top: 8px;
                    padding-left: 8px;
                    font-size: 12px;
                    color: #717171;
                    &:first-child {
                        margin-top: 0;
                    }
                    &::before {
                        content: 'Â·';
                        padding-right: 6px;
                    }
                }
            }
        }
        .info {
            table {
                width: 100%;
                margin-top: 45px;
                text-align: left;
                font-size: 12px;
                color: #1f1f1f;
                border-top: 2px solid #1f1f1f;
                border-bottom: 2px solid #1f1f1f;
                border-collapse: collapse;
                caption {
                    display: none;
                }
                th, td {
                    padding: 14px 0;
                    vertical-align: middle;
                    border-bottom: 1px solid #f1f1f1;
                }
                th {
                    font-weight: bold;
                    vertical-align: top;
                    span {
                        display: block;
                        margin-top: 3px;
                        font-weight: normal;
                    }
                }
            }
            .fiber {
                margin-top: 30px;
                &::after {
                    content: '';
                    display: table;
                    clear: both;
                }
                .thumb {
                    float: left;
                    img {
                        width: 80px;
                        height: 80px;
                    }
                }
                .txt {
                    margin-left: 100px;
                    dt {
                        padding-top: 11px;
                        font-weight: bold;
                        font-size: 14px;
                        color: #1f1f1f;
                    }
                    dd {
                        margin-top: 10px;
                        font-size: 12px;
                        color: #898989;
                        line-height: 1.4;
                    }
                }
            }
        }
        .wash {
            ul {
                &::after {
                    content: '';
                    display: table;
                    clear: both;
                }
                .wash-list {
                    position: relative;
                    float: left;
                    width: 50%;
                    min-height: 26px;
                    margin: 10px 0;
                    padding-left: 42px;
                    font-size: 12px;
                    color: #1f1f1f;
                    line-height: 1.4;
                    box-sizing: border-box;
                    letter-spacing: -.4px;
